*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

::selection {
  color: #fff;
  background-color: #047449; }

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
  scroll-padding-top: 7.9rem;
  overflow-x: hidden; }

body {
  overflow-x: hidden;
  background-color: #212121;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  color: #fff;
  display: grid;
  grid-template-columns: repeat(12, 1fr); }

.text--thin {
