h4 {
  font-size: 3rem; }

p {
  font-size: 2.1rem;
  color: #fff; }

h1,
h2,
h3,
h4 {
  line-height: 1.2; }

.mb-large {
  margin-bottom: 5rem; }

.mb-big {
  margin-bottom: 3rem; }

.mb-medium {
  margin-bottom: 2rem; }

.mb-small {
  margin-bottom: 1rem; }

.mt-large {
  margin-top: 5rem; }

.mt-big {
  margin-top: 3rem; }

.mt-medium {
  margin-top: 2rem; }

.mt-small {
  margin-top: 1rem; }

.btn {
  display: inline-block;
  background-color: #047449;
  border-radius: 150px;
  padding: 0.7rem 5rem;
  font-size: 3rem;
  font-family: inherit;
  text-decoration: none;
  color: #fff;
  position: relative;
  backface-visibility: hidden;
  z-index: 1;
  overflow: hidden;
  transition: all 0.3s ease-in;
  outline: none;
  border: none; }
  .btn::before {
    height: 100%;
    content: '';
    z-index: -1;
    position: absolute;
    width: 100%;
    background-color: #03603d;
    transition: transform 1s cubic-bezier(0.68, -0.6, 0.32, 1.6);
    top: 0;
    left: 0;
    transform: translateX(110%) scale(1.1);
    border-radius: 150px;
    backface-visibility: hidden; }
  .btn:hover {
    color: #fff !important;
    cursor: pointer; }
  .btn:hover::before {
    transform: translateX(0) scale(1.1); }

.scroll {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  width: 3rem;
  height: 5rem;
  border-radius: 100px;
  background-color: transparent;
  border: 4px solid #fff;
  animation: animateScroll 1s ease-in infinite;
  transition: all 0.5s ease-in; }
  .scroll:hover {
    border-color: #047449; }
  .scroll:hover::before {
    background-color: #047449; }
  .scroll::before {
    content: '';
    position: absolute;
    bottom: 0.3rem;
    left: 50%;
    transform: translateX(-50%);
    width: 1.6rem;
    height: 1.6rem;
    background-color: #fff;
    border-radius: 50%;
    transition: all 0.5s ease-in; }

@keyframes animateScroll {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(0.5rem); }
  100% {
    transform: translateY(0); } }

.bg-video {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  overflow: hidden; }
  .bg-video__content {
    height: 100%;
    width: 100%;
    object-fit: cover; }

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #212121;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center; }
  .overlay i {
    margin-top: 3rem;
    font-size: 8rem;
    color: #047449; }
  .overlay__content {
    opacity: 0; }

.card {
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center; }
  .card__img {
    position: relative; }
    .card__img img {
      display: block;
      width: 100%;
      height: auto;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px; }
    .card__img::before {
      content: '';
      position: absolute;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(4, 116, 73, 0.8); }
    .card__img h4 {
      width: 90%;
      font-size: 3.5rem;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }
  .card__content {
    padding: 3.5rem; }
    .card__content p {
      color: #212121;
      margin-bottom: 3.5rem; }

.card__small {
  border-radius: 10px;
  background-color: #fff;
  color: #212121;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 8rem 10rem;
  text-align: center; }
  .card__small h4 {
    font-weight: 600;
    margin-bottom: 1rem; }
  .card__small p {
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 300;
    color: #212121; }

.card__big {
  padding: 3rem;
  position: relative;
  grid-row: 1 / span 2;
  align-self: stretch;
  background-color: #fff;
  border-radius: 10px;
  text-align: center; }
  .card__big__content {
    color: #212121;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center; }
    .card__big__content p {
      color: #212121;
      font-size: 2rem; }
    .card__big__content .label {
      margin: 5rem 0 3rem 0; }
      .card__big__content .label h3 {
        font-weight: 600; }
      .card__big__content .label p {
        text-transform: uppercase;
        letter-spacing: 3px;
        font-weight: 300;
        color: #212121; }
  .card__big img {
    width: 35%;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%; }

.main {
  grid-column: 1/-1; }

.section {
  grid-column: 1 / end;
  display: grid;
  grid-template-columns: repeat(2, 1fr); }
  .section .content {
    padding: 8vh 10rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    min-height: 70rem; }
    .section .content a {
      font-weight: 600; }
  .section .img {
    position: relative;
    overflow: hidden; }
    .section .img img {
      width: auto;
      height: 100%;
      min-width: 100%; }
    .section .img::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(4, 116, 73, 0.65); }
    .section .img h2 {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      letter-spacing: 5px; }

.img-left .img {
  grid-row: 1/2;
  grid-column: 1/2; }

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 8vh;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.5s ease-in; }
  .header__burger {
    display: none; }
  .header__branding {
    margin-left: 3%;
