.header.active {
  background-color: #141414; }

.section-hero {
  padding-top: 8vh;
  grid-column: 1/-1;
  min-height: 100vh;
  position: relative;
  background-image: radial-gradient(rgba(33, 33, 33, 0.8), #212121);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.section-cards {
  padding: 5rem 0;
  grid-column: 2/-2;
  display: grid;
  grid-gap: 5rem;
  justify-content: center;
  align-items: stretch;
  grid-template-columns: repeat(auto-fit, minmax(min-content, 45rem)); }

.section-managment {
  grid-column: 2/-2;
  padding: 10rem 0 5rem 0;
  display: grid;
  grid-column-gap: 5rem;
  grid-template-columns: repeat(auto-fit, minmax(min-content, 45rem));
  grid-template-rows: repeat(2, 1fr);
  justify-content: center;
  align-items: center; }
  .section-managment .card__small:nth-of-type(odd) {
    grid-row: 1/2; }
  .section-managment .card__small:nth-of-type(even) {
